import Vue from 'vue'
import Router from 'vue-router'


import Products from '../pages/products'
import Index from '../pages/index'
import Contact from '../pages/contact'

Vue.use(Router);

let router = new Router({
    mode: "history",
    routes: [{
            path: '/',
            name: 'index',
            component: Index,
            props: true
        },
        {
            path: '/product',
            name: 'products',
            component: Products,
            props: true
        },
        {
            path: '/contact',
            name: 'contact',
            component: Contact,
        }
    ]
})

export default router;